import { useState } from 'react';
import { TypesList } from './TypesList';
import { DocumentationArea } from './DocumentationArea';
import { Drawer } from './Drawer';
import { DocumentationPanel } from './DocumentationPanel';
import { ApiField, SavedDoc } from '../types';
import { fieldService } from '../services/fieldService';
import { ChevronLeft, ChevronRight } from 'lucide-react';

interface DocumentationBuilderProps {
  doc: SavedDoc;
}

export function DocumentationBuilder({ doc }: DocumentationBuilderProps) {
  const [fields, setFields] = useState<ApiField[]>(doc.fields);
  const [selectedField, setSelectedField] = useState<ApiField | null>(null);
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);

  const handleDrop = (newField: ApiField) => {
    if (selectedField && (selectedField.type === 'CompositeField' || selectedField.type === 'FieldGroup')) {
      const updatedField = {
        ...selectedField,
        children: [...(selectedField.children || []), newField]
      };
      handleFieldUpdate(updatedField);
    } else {
      setFields((prev) => [...prev, newField]);
    }
  };

  const handleAddField = (index: number, field: ApiField) => {
    setFields(prev => {
      const newFields = [...prev];
      newFields.splice(index + 1, 0, field);
      return newFields;
    });
  };

  const handleFieldClick = (field: ApiField) => {
    setSelectedField(field);
    setIsDrawerOpen(true);
  };

  const handleFieldUpdate = (updatedField: ApiField) => {
    setFields((prev) => fieldService.updateFieldInTree(prev, updatedField));
    setSelectedField(updatedField);
  };

  const handleFieldRemove = (fieldId: string) => {
    setFields((prev) => fieldService.removeFieldFromTree(prev, fieldId));
    if (selectedField?.id === fieldId) {
      setIsDrawerOpen(false);
      setSelectedField(null);
    }
  };

  const handleFieldMove = (fieldId: string, targetId: string | null, position: 'before' | 'after' | 'inside') => {
    setFields(prev => {
      const [field, remainingFields] = fieldService.extractFieldFromTree(prev, fieldId);
      if (!field) return prev;

      if (!targetId) {
        return position === 'before' ? [field, ...remainingFields] : [...remainingFields, field];
      }

      return fieldService.insertFieldIntoTree(remainingFields, field, targetId, position);
    });
  };

  return (
    <div className="flex h-full w-full">
      <div className={`relative transition-all duration-300 ${isSidebarOpen ? 'w-300' : 'w-0'}`}>
        <TypesList onDragStart={() => {}} className={isSidebarOpen ? '' : 'hidden'} />
        <button
          onClick={() => setIsSidebarOpen(!isSidebarOpen)}
          className={`absolute ${isSidebarOpen ? '-right-3' : '-right-4'}  top-1/2 transform -translate-y-1/2 bg-white p-1 rounded-full shadow-md border z-10`}
        >
          {isSidebarOpen ? <ChevronLeft className="h-4 w-4" /> : <ChevronRight className="h-4 w-4" />}
        </button>
      </div>
      <DocumentationArea
        fields={fields}
        onDrop={handleDrop}
        onFieldClick={handleFieldClick}
        onFieldRemove={handleFieldRemove}
        onFieldMove={handleFieldMove}
        onFieldUpdate={handleFieldUpdate}
        selectedField={selectedField}
        onAddField={handleAddField}
        doc={doc}
      />

      <DocumentationPanel
        fields={fields}
        onLoadFields={setFields}
        doc={doc}
      />
    
      <Drawer
        isOpen={isDrawerOpen}
        onClose={() => setIsDrawerOpen(false)}
        field={selectedField}
        onFieldUpdate={handleFieldUpdate}
      />
    </div>
  );
}