import { useState } from 'react';
import { ApiField, SavedDoc } from '../types';
import { generateOpenAPISpec } from '../utils/openapi';
import { OpenAPIModal } from './OpenAPIModal';
import { FormGenerator } from './FormGenerator';
import { Toolbar } from './Toolbar';
import { FieldList } from './FieldList';
import { TEXT } from '../constants/text';

interface DocumentationAreaProps {
  fields: ApiField[];
  onDrop: (field: ApiField) => void;
  onFieldClick: (field: ApiField) => void;
  onFieldRemove: (fieldId: string) => void;
  onFieldMove: (fieldId: string, targetId: string | null, position: 'before' | 'after' | 'inside') => void;
  onAddField: (index: number, field: ApiField) => void;
  onFieldUpdate: (field: ApiField) => void;
  selectedField: ApiField | null;
  doc: SavedDoc;
}

export function DocumentationArea({
  fields, 
  onDrop, 
  onFieldClick, 
  onFieldRemove,
  onFieldMove,
  onAddField,
  onFieldUpdate,
  selectedField,
  doc
}: DocumentationAreaProps) {
  const [isOpenAPIModalOpen, setIsOpenAPIModalOpen] = useState(false);
  const [showFormGenerator, setShowFormGenerator] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');

  const filteredFields = fields.filter(field => 
    field.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    field.description?.toLowerCase().includes(searchQuery.toLowerCase()) ||
    field.type.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <div 
      className="documentation-area flex-1 p-6 bg-white overflow-y-auto relative"
      onDragOver={(e) => e.preventDefault()}
      onDrop={(e) => {
        e.preventDefault();
        const fieldData = e.dataTransfer.getData('application/json');
        if (fieldData) {
          const field = JSON.parse(fieldData) as ApiField;
          onDrop({ ...field, id: `${field.id}-${Date.now()}` });
        }
      }}
    >
      <div className="min-w-90 max-w-3xl mx-auto">
        <Toolbar
          onSearch={setSearchQuery}
          onImport={(importedFields) => importedFields.forEach(field => onDrop({ ...field, id: `${field.id}-${Date.now()}` }))}
          onExport={() => {
            const blob = new Blob([JSON.stringify(fields, null, 2)], { type: 'application/json' });
            const url = URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.href = url;
            a.download = `${doc.name.toLowerCase().replace(/\s+/g, '-')}-api-documentation.json`;
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
            URL.revokeObjectURL(url);
          }}
          onOpenAPIClick={() => setIsOpenAPIModalOpen(true)}
          onFormGeneratorClick={() => setShowFormGenerator(true)}
          hasFields={fields.length > 0}
          searchQuery={searchQuery}
        />
        
        {filteredFields.length === 0 ? (
          <div className="border-2 border-dashed border-gray-300 rounded-lg p-8 text-center">
            <p className="text-gray-500">
              {fields.length === 0 
                ? TEXT.FIELDS.DRAG_DROP
                : TEXT.FIELDS.NO_RESULTS}
            </p>
          </div>
        ) : (
          <FieldList
            fields={filteredFields}
            selectedField={selectedField}
            onFieldClick={onFieldClick}
            onFieldRemove={onFieldRemove}
            onFieldMove={onFieldMove}
            onAddField={onAddField}
            onFieldUpdate={onFieldUpdate}
          />
        )}
      </div>

      {isOpenAPIModalOpen && (
        <OpenAPIModal
          isOpen={isOpenAPIModalOpen}
          onClose={() => setIsOpenAPIModalOpen(false)}
          spec={generateOpenAPISpec(fields)}
        />
      )}

      {showFormGenerator && (
        <FormGenerator
          isOpen={showFormGenerator}
          onClose={() => setShowFormGenerator(false)}
          fields={fields}
        />
      )}
    </div>
  );
}