import { type DragEvent, useState, useRef, useEffect } from 'react';
import { Plus, GripVertical, Trash2, Settings } from 'lucide-react';
import { ApiField } from '../types';

interface FieldListProps {
  fields: ApiField[];
  selectedField: ApiField | null;
  onFieldClick: (field: ApiField) => void;
  onFieldRemove: (fieldId: string) => void;
  onFieldMove: (fieldId: string, targetId: string | null, position: 'before' | 'after' | 'inside') => void;
  onAddField: (index: number, field: ApiField) => void;
  onFieldUpdate: (field: ApiField) => void;
}

export function FieldList({
  fields,
  selectedField,
  onFieldClick,
  onFieldRemove,
  onFieldMove,
  onAddField,
  onFieldUpdate
}: FieldListProps) {
  const [draggedField, setDraggedField] = useState<string | null>(null);
  const [dropTarget, setDropTarget] = useState<{ id: string | null, position: 'before' | 'after' | 'inside' } | null>(null);
  const [editingFieldId, setEditingFieldId] = useState<string | null>(null);
  const [editingName, setEditingName] = useState('');
  const editInputRef = useRef<HTMLInputElement>(null);
  const [lastScrollPosition, setLastScrollPosition] = useState(0);

  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (editInputRef.current && !editInputRef.current.contains(event.target as Node)) {
        handleNameEditComplete();
      }
    }

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [editingFieldId, editingName]);

  const handleFieldDragStart = (e: DragEvent, fieldId: string) => {
    e.stopPropagation();
    setDraggedField(fieldId);
    e.dataTransfer.effectAllowed = 'move';
  };

  const handleFieldDragEnd = () => {
    setDraggedField(null);
    setDropTarget(null);
  };

  const handleFieldDragOver = (e: DragEvent, fieldId: string, field: ApiField) => {
    e.preventDefault();
    e.stopPropagation();

    if (!draggedField || draggedField === fieldId) return;

    const rect = (e.currentTarget as HTMLElement).getBoundingClientRect();
    const y = e.clientY - rect.top;
    const height = rect.height;

    let position: 'before' | 'after' | 'inside';
    if (y < height * 0.25) {
      position = 'before';
    } else if (y > height * 0.75) {
      position = 'after';
    } else if (field.type === 'CompositeField' || field.type === 'FieldGroup') {
      position = 'inside';
    } else {
      position = y < height / 2 ? 'before' : 'after';
    }

    setDropTarget({ id: fieldId, position });
  };

  const handleFieldDrop = (e: DragEvent, targetId: string) => {
    e.preventDefault();
    e.stopPropagation();

    if (!draggedField || !dropTarget) return;

    const targetField = fields.find(f => f.id === targetId);
    if (dropTarget.position === 'inside' && 
        targetField && 
        targetField.type !== 'CompositeField' && 
        targetField.type !== 'FieldGroup') {
      return;
    }

    onFieldMove(draggedField, targetId, dropTarget.position);
    setDraggedField(null);
    setDropTarget(null);
  };

  const handleAddNewField = (index: number) => {
    // Store current scroll position
    const container = document.querySelector('.documentation-area');
    if (container) {
      setLastScrollPosition(container.scrollTop);
    }

    const newField: ApiField = {
      id: `text-field-${Date.now()}`,
      name: 'New Text Field',
      type: 'TextField',
      description: 'Enter text here',
      required: false,
      config: {}
    };
    onAddField(index, newField);

    // Restore scroll position after a short delay to allow for DOM update
    setTimeout(() => {
      if (container) {
        container.scrollTop = lastScrollPosition;
      }
    }, 0);
  };

  const handleNameEdit = (field: ApiField) => {
    setEditingFieldId(field.id);
    setEditingName(field.name);
  };

  const handleNameEditComplete = () => {
    if (editingFieldId && editingName.trim()) {
      const field = fields.find(f => f.id === editingFieldId);
      if (field) {
        onFieldUpdate({ ...field, name: editingName.trim() });
      }
    }
    setEditingFieldId(null);
    setEditingName('');
  };

  const renderField = (field: ApiField, index: number, depth = 0) => {
    const isDropTarget = dropTarget?.id === field.id;
    const dropPosition = dropTarget?.position;
    const isSelected = selectedField?.id === field.id;
    const isEditing = editingFieldId === field.id;

    return (
      <div key={field.id}>
        <div
          className="group relative"
          style={{ marginLeft: `${depth * 1}rem` }}
        >
          <div className="absolute -top-3 left-0 right-0 h-6 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity">
            <button
              onClick={() => handleAddNewField(index)}
              className="bg-blue-500 hover:bg-blue-600 text-white rounded-full p-1 shadow-lg transform hover:scale-110 transition-transform"
            >
              <Plus className="h-4 w-4" />
            </button>
          </div>

          <div className={`relative ${isDropTarget ? 'z-10' : ''}`}>
            {isDropTarget && dropPosition === 'before' && (
              <div className="absolute -top-1 left-0 right-0 h-1 bg-blue-500 rounded" />
            )}
            <div
              draggable
              onDragStart={(e) => handleFieldDragStart(e, field.id)}
              onDragEnd={handleFieldDragEnd}
              onDragOver={(e) => handleFieldDragOver(e, field.id, field)}
              onDrop={(e) => handleFieldDrop(e, field.id)}
              className={`border rounded-lg p-2 hover:shadow-md transition-shadow ${
                isDropTarget && dropPosition === 'inside' && (field.type === 'CompositeField' || field.type === 'FieldGroup') 
                  ? 'border-blue-500 bg-blue-50' 
                  : ''
              } ${isSelected ? 'border-blue-500 ring-1 ring-blue-500' : ''}`}
            >
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <div className="cursor-move p-1 hover:bg-gray-100 rounded">
                    <GripVertical className="h-4 w-4 text-gray-400" />
                  </div>
                  <div className="flex-1">
                    <div className="flex items-center space-x-2">
                      {isEditing ? (
                        <input
                          ref={editInputRef}
                          type="text"
                          value={editingName}
                          onChange={(e) => setEditingName(e.target.value)}
                          onKeyDown={(e) => {
                            if (e.key === 'Enter') handleNameEditComplete();
                            if (e.key === 'Escape') {
                              setEditingFieldId(null);
                              setEditingName('');
                            }
                          }}
                          className="px-2 py-1 border rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
                          autoFocus
                        />
                      ) : (
                        <span 
                          className="font-medium cursor-text hover:bg-gray-100 px-2 py-1 rounded"
                          onClick={() => handleNameEdit(field)}
                        >
                          {field.name}
                        </span>
                      )}
                      <span className="text-sm text-gray-500">{field.type}</span>
                      {field.required && (
                        <span className="text-xs bg-red-100 text-red-800 px-2 py-0.5 rounded">Required</span>
                      )}
                    </div>
                    {field.description && (
                      <p className="text-sm text-gray-600 mt-1">{field.description}</p>
                    )}
                  </div>
                </div>
                <div className="flex items-center gap-2">
                  {(field.type === 'CompositeField' || field.type === 'FieldGroup') && (
                    <button
                      onClick={() => handleAddNewField(index)}
                      className="p-1 hover:bg-gray-100 rounded"
                      title="Add child field"
                    >
                      <Plus className="h-4 w-4 text-gray-500" />
                    </button>
                  )}
                  <button
                    onClick={() => onFieldClick(field)}
                    className="p-1 hover:bg-gray-100 rounded"
                    title="Field settings"
                  >
                    <Settings className="h-4 w-4 text-gray-500" />
                  </button>
                  <button
                    onClick={() => onFieldRemove(field.id)}
                    className="p-1 hover:bg-gray-100 rounded"
                  >
                    <Trash2 className="h-4 w-4 text-gray-500" />
                  </button>
                </div>
              </div>
              {field.children && field.children.length > 0 && (
                <div className="mt-2 space-y-2">
                  {field.children.map((child, childIndex) => renderField(child, childIndex, depth + 1))}
                </div>
              )}
            </div>
            {isDropTarget && dropPosition === 'after' && (
              <div className="absolute -bottom-1 left-0 right-0 h-1 bg-blue-500 rounded" />
            )}
          </div>
        </div>
        
        {index === fields.length - 1 && (
          <div className="group relative h-6 mt-2">
            <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity">
              <button
                onClick={() => handleAddNewField(index + 1)}
                className="bg-blue-500 hover:bg-blue-600 text-white rounded-full p-1 shadow-lg transform hover:scale-110 transition-transform"
              >
                <Plus className="h-4 w-4" />
              </button>
            </div>
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="space-y-2">
      {fields.map((field, index) => renderField(field, index))}
    </div>
  );
}