import { type FormEvent, useState, useEffect } from 'react';
import { X, Trash2, ChevronLeft, ChevronRight, Edit2, Check, XCircle } from 'lucide-react';
import { ApiField, FormData } from '../types';

interface FormGeneratorProps {
  isOpen: boolean;
  onClose: () => void;
  fields: ApiField[];
}

interface FormStep {
  title: string;
  fields: ApiField[];
}

export function FormGenerator({ isOpen, onClose, fields }: FormGeneratorProps) {
  const [formData, setFormData] = useState<Record<string, any>>({});
  const [savedData, setSavedData] = useState<FormData[]>([]);
  const [showTable, setShowTable] = useState(true);
  const [currentStep, setCurrentStep] = useState(0);
  const [steps, setSteps] = useState<FormStep[]>([]);
  const [editingEntry, setEditingEntry] = useState<string | null>(null);
  const [editedData, setEditedData] = useState<Record<string, any>>({});
  const [errors, setErrors] = useState<Record<string, string>>({});

  useEffect(() => {
    const saved = localStorage.getItem('formData');
    if (saved) {
      setSavedData(JSON.parse(saved));
    }

    const formSteps: FormStep[] = [];
    let currentStepFields: ApiField[] = [];

    fields.forEach(field => {
      if (field.type === 'FieldGroup') {
        if (currentStepFields.length > 0) {
          formSteps.push({ title: 'Step ' + (formSteps.length + 1), fields: currentStepFields });
          currentStepFields = [];
        }
        formSteps.push({ title: field.name, fields: field.children || [] });
      } else {
        currentStepFields.push(field);
      }
    });

    if (currentStepFields.length > 0) {
      formSteps.push({ title: 'Step ' + (formSteps.length + 1), fields: currentStepFields });
    }

    if (formSteps.length === 0) {
      formSteps.push({ title: 'Form Fields', fields });
    }

    setSteps(formSteps);
  }, [fields]);

  const validateForm = () => {
    const newErrors: Record<string, string> = {};
    steps[currentStep].fields.forEach(field => {
      if (field.required && !formData[field.name]) {
        newErrors[field.name] = 'This field is required';
      }
    });
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    if (validateForm()) {
      const newEntry: FormData = {
        id: Date.now().toString(),
        data: formData,
        createdAt: new Date().toString()
      };
      const updatedData = [newEntry, ...savedData];
      setSavedData(updatedData);
      localStorage.setItem('formData', JSON.stringify(updatedData));
      setFormData({});
      setCurrentStep(0);
      setErrors({});
    }
  };

  const handleDelete = (id: string) => {
    const updatedData = savedData.filter(entry => entry.id !== id);
    setSavedData(updatedData);
    localStorage.setItem('formData', JSON.stringify(updatedData));
  };

  const handleNext = () => {
    if (validateForm() && currentStep < steps.length - 1) {
      setCurrentStep(currentStep + 1);
    }
  };

  const handlePrevious = () => {
    if (currentStep > 0) {
      setCurrentStep(currentStep - 1);
    }
  };

  const handleEdit = (entry: FormData) => {
    setEditingEntry(entry.id);
    setEditedData(entry.data);
  };

  const handleSaveEdit = (id: string) => {
    const updatedData = savedData.map(entry => {
      if (entry.id === id) {
        return {
          ...entry,
          data: editedData
        };
      }
      return entry;
    });
    setSavedData(updatedData);
    localStorage.setItem('formData', JSON.stringify(updatedData));
    setEditingEntry(null);
    setEditedData({});
  };

  const handleCancelEdit = () => {
    setEditingEntry(null);
    setEditedData({});
  };

  const renderField = (field: ApiField) => {
    const fieldId = `field-${field.id}`;
    const hasError = errors[field.name];

    const inputClasses = `
      field__input
      w-full
      px-3
      py-2
      border
      rounded
      focus:outline-none
      focus:ring-2
      focus:ring-[#005a93]
      ${hasError ? 'border-red-500' : 'border-gray-300'}
      ${field.config?.disabled ? 'bg-[#ebeff2]' : 'bg-white'}
    `.trim();

    return (
      <div key={field.id} className="field mb-6">
        <div className="field__header flex items-center mb-1">
          {field.config?.leftTitle && (
            <span className="field__left-title text-sm text-[#566b8d] mr-2">
              {field.config.leftTitle}
            </span>
          )}
          <label htmlFor={fieldId} className="field__label text-sm font-medium text-[#43536d]">
            {field.name}
            {field.required && <span className="text-red-500 ml-1">*</span>}
          </label>
          {field.config?.rightTitle && (
            <span className="field__right-title text-sm text-[#566b8d] ml-2">
              {field.config.rightTitle}
            </span>
          )}
        </div>
        
        {field.description && (
          <div className="field__description text-sm text-[#566b8d] mb-2">
            {field.description}
          </div>
        )}

        <div className="field__input-holder">
          {(() => {
            switch (field.type) {
              case 'TextareaField':
                return (
                  <textarea
                    id={fieldId}
                    value={formData[field.name] || ''}
                    onChange={(e) => {
                      setFormData(prev => ({ ...prev, [field.name]: e.target.value }));
                      if (errors[field.name]) {
                        setErrors(prev => ({ ...prev, [field.name]: '' }));
                      }
                    }}
                    rows={field.config?.rows || 4}
                    className={`${inputClasses} min-h-[100px]`}
                    placeholder={field.config?.placeholder}
                    disabled={field.config?.disabled}
                    readOnly={field.config?.readonly}
                  />
                );
              case 'CheckboxField':
                return (
                  <div className="flex items-center">
                    <input
                      type="checkbox"
                      id={fieldId}
                      checked={formData[field.name] || false}
                      onChange={(e) => setFormData(prev => ({ ...prev, [field.name]: e.target.checked }))}
                      className="form-checkbox h-4 w-4 text-[#005a93] rounded border-gray-300"
                      disabled={field.config?.disabled}
                    />
                    <label htmlFor={fieldId} className="ml-2 text-sm text-[#43536d]">
                      {field.config?.placeholder || field.name}
                    </label>
                  </div>
                );
              case 'DropdownField':
                return (
                  <select
                    id={fieldId}
                    value={formData[field.name] || ''}
                    onChange={(e) => {
                      setFormData(prev => ({ ...prev, [field.name]: e.target.value }));
                      if (errors[field.name]) {
                        setErrors(prev => ({ ...prev, [field.name]: '' }));
                      }
                    }}
                    className={inputClasses}
                    disabled={field.config?.disabled}
                  >
                    <option value="">{field.config?.placeholder || 'Select an option'}</option>
                    {Object.entries(field.config?.options || {}).map(([value, label]) => (
                      <option key={value} value={value}>{label}</option>
                    ))}
                  </select>
                );
              default:
                return (
                  <input
                    type="text"
                    id={fieldId}
                    value={formData[field.name] || ''}
                    onChange={(e) => {
                      setFormData(prev => ({ ...prev, [field.name]: e.target.value }));
                      if (errors[field.name]) {
                        setErrors(prev => ({ ...prev, [field.name]: '' }));
                      }
                    }}
                    className={inputClasses}
                    placeholder={field.config?.placeholder}
                    disabled={field.config?.disabled}
                    readOnly={field.config?.readonly}
                  />
                );
            }
          })()}
        </div>

        {hasError && (
          <div className="field__error text-sm text-red-500 mt-1">
            {errors[field.name]}
          </div>
        )}
      </div>
    );
  };

  return (
    <div className={`fixed inset-y-0 right-0 w-[90%] bg-white shadow-2xl transform transition-transform duration-300 ${isOpen ? 'translate-x-0' : 'translate-x-full'}`}>
      <div className="h-full flex flex-col">
        <div className="bg-[#005a93] text-white p-6 flex justify-between items-center">
          <div className="flex items-center gap-4">
            <h2 className="text-2xl font-semibold">Form Generator</h2>
            <button
              onClick={() => setShowTable(!showTable)}
              className="px-4 py-2 text-sm font-medium bg-[#004b7a] hover:bg-[#003d61] rounded-md transition-colors"
            >
              {showTable ? 'Show Form' : 'Show Data'}
            </button>
          </div>
          <button
            onClick={onClose}
            className="p-2 hover:bg-[#004b7a] rounded-full transition-colors"
          >
            <X className="h-6 w-6" />
          </button>
        </div>
        
        <div className="flex-1 overflow-auto">
          {showTable ? (
            <div className="p-6">
              <div className="bg-white shadow-md rounded-lg overflow-hidden">
                <table className="min-w-full divide-y divide-gray-200">
                  <thead className="bg-[#ebeff2]">
                    <tr>
                      {fields.filter(f => !f.hideFromApi).map(field => (
                        <th
                          key={field.id}
                          className="px-6 py-3 text-left text-xs font-medium text-[#43536d] uppercase tracking-wider"
                        >
                          {field.name}
                        </th>
                      ))}
                      <th className="px-6 py-3 text-right text-xs font-medium text-[#43536d] uppercase tracking-wider">
                        Actions
                      </th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-gray-200">
                    {savedData.map((entry) => (
                      <tr key={entry.id} className="hover:bg-[#f6f7f8]">
                        {fields.filter(f => !f.hideFromApi).map(field => (
                          <td key={field.id} className="px-6 py-4 whitespace-nowrap text-sm text-[#43536d]">
                            {editingEntry === entry.id ? (
                              <input
                                type="text"
                                value={editedData[field.name] || ''}
                                onChange={(e) => setEditedData(prev => ({
                                  ...prev,
                                  [field.name]: e.target.value
                                }))}
                                className="w-full px-2 py-1 border rounded focus:outline-none focus:ring-2 focus:ring-[#005a93]"
                              />
                            ) : (
                              entry.data[field.name]?.toString() || '-'
                            )}
                          </td>
                        ))}
                        <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                          {editingEntry === entry.id ? (
                            <div className="flex items-center justify-end space-x-2">
                              <button
                                onClick={() => handleSaveEdit(entry.id)}
                                className="text-green-600 hover:text-green-900"
                                title="Save"
                              >
                                <Check className="h-4 w-4" />
                              </button>
                              <button
                                onClick={handleCancelEdit}
                                className="text-gray-600 hover:text-gray-900"
                                title="Cancel"
                              >
                                <XCircle className="h-4 w-4" />
                              </button>
                            </div>
                          ) : (
                            <div className="flex items-center justify-end space-x-2">
                              <button
                                onClick={() => handleEdit(entry)}
                                className="text-[#005a93] hover:text-[#004b7a]"
                                title="Edit"
                              >
                                <Edit2 className="h-4 w-4" />
                              </button>
                              <button
                                onClick={() => handleDelete(entry.id)}
                                className="text-red-600 hover:text-red-900"
                                title="Delete"
                              >
                                <Trash2 className="h-4 w-4" />
                              </button>
                            </div>
                          )}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          ) : (
            <div className="max-w-3xl mx-auto p-6">
              <div className="mb-8">
                <div className="flex items-center justify-between mb-4">
                  <h3 className="text-xl font-semibold text-[#43536d]">{steps[currentStep].title}</h3>
                  <div className="text-sm text-[#566b8d]">
                    Step {currentStep + 1} of {steps.length}
                  </div>
                </div>
                <div className="w-full bg-[#ebeff2] rounded-full h-2">
                  <div
                    className="bg-[#005a93] h-2 rounded-full transition-all duration-300"
                    style={{ width: `${((currentStep + 1) / steps.length) * 100}%` }}
                  />
                </div>
              </div>

              <form onSubmit={handleSubmit} className="space-y-6">
                <div className="space-y-6">
                  {steps[currentStep].fields.map(field => renderField(field))}
                </div>
                
                <div className="flex justify-between pt-6 border-t">
                  <button
                    type="button"
                    onClick={handlePrevious}
                    className={`flex items-center px-4 py-2 border border-[#005a93] rounded-md shadow-sm text-sm font-medium text-[#005a93] bg-white hover:bg-[#f6f7f8] focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-[#005a93] ${
                      currentStep === 0 ? 'invisible' : ''
                    }`}
                  >
                    <ChevronLeft className="h-4 w-4 mr-2" />
                    Previous
                  </button>
                  
                  {currentStep === steps.length - 1 ? (
                    <button
                      type="submit"
                      className="flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-[#005a93] hover:bg-[#004b7a] focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-[#005a93]"
                    >
                      Submit
                    </button>
                  ) : (
                    <button
                      type="button"
                      onClick={handleNext}
                      className="flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-[#005a93] hover:bg-[#004b7a] focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-[#005a93]"
                    >
                      Next
                      <ChevronRight className="h-4 w-4 ml-2" />
                    </button>
                  )}
                </div>
              </form>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}