import { SavedDoc } from '../types';
import wretch from 'wretch';

export const api =
  wretch("http://ssbase.test/admin/api-builder", { mode: "cors" })
    .errorType("json")
    .resolve(r => r.json())

export const createApi = async (newDoc: SavedDoc) => {
    return await api.url("/addCollection").post(newDoc);
}

export const updateApi = async (newDoc: SavedDoc) => {
    return  await api.url(`/updateCollection/${newDoc.id}`).put(newDoc);
}