import { type ChangeEvent, useRef } from 'react';
import { Search } from 'lucide-react';
import { ApiField } from '../types';
import { ActionDropdown } from './ActionDropdown';
import { TEXT } from '../constants/text';

interface ToolbarProps {
  onSearch: (query: string) => void;
  onImport: (fields: ApiField[]) => void;
  onExport: () => void;
  onOpenAPIClick: () => void;
  onFormGeneratorClick: () => void;
  hasFields: boolean;
  searchQuery: string;
}

export function Toolbar({
  onSearch,
  onImport,
  onExport,
  onOpenAPIClick,
  onFormGeneratorClick,
  hasFields,
  searchQuery
}: ToolbarProps) {
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleImport = () => {
    fileInputRef.current?.click();
  };

  const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const importedFields = JSON.parse(e.target?.result as string);
          onImport(importedFields);
        } catch (error) {
          console.error('Error importing fields:', error);
        }
      };
      reader.readAsText(file);
    }
  };

  return (
    <div className="flex justify-between items-center mb-6 relative z-10">
      <div className="relative flex-1 max-w-md">
        <input
          type="text"
          placeholder={TEXT.FIELDS.SEARCH}
          value={searchQuery}
          onChange={(e) => onSearch(e.target.value)}
          className="w-full pl-10 pr-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#005a93]"
        />
        <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
      </div>

      <input
        ref={fileInputRef}
        type="file"
        accept=".json"
        onChange={handleFileChange}
        className="hidden"
      />

      <ActionDropdown
        onImport={handleImport}
        onExport={onExport}
        onOpenAPI={onOpenAPIClick}
        onFormGenerator={onFormGeneratorClick}
        hasFields={hasFields}
      />
    </div>
  );
}