import { useState, useRef, useEffect } from 'react';
import { MoreVertical, Upload, Download, FileJson, File } from 'lucide-react';
import { TEXT } from '../constants/text';

interface ActionDropdownProps {
  onImport: () => void;
  onExport: () => void;
  onOpenAPI: () => void;
  onFormGenerator: () => void;
  hasFields: boolean;
}

export function ActionDropdown({
  onImport,
  onExport,
  onOpenAPI,
  onFormGenerator,
  hasFields
}: ActionDropdownProps) {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setIsOpen(false);
      }
    }

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  const actions = [
    {
      icon: Upload,
      label: TEXT.DOCS.ACTIONS.IMPORT,
      onClick: onImport,
      disabled: false
    },
    {
      icon: Download,
      label: TEXT.DOCS.ACTIONS.EXPORT,
      onClick: onExport,
      disabled: !hasFields
    },
    {
      icon: FileJson,
      label: TEXT.DOCS.ACTIONS.OPENAPI,
      onClick: onOpenAPI,
      disabled: !hasFields
    },
    {
      icon: File,
      label: TEXT.DOCS.ACTIONS.FORM,
      onClick: onFormGenerator,
      disabled: !hasFields
    }
  ];

  return (
    <div className="relative" ref={dropdownRef}>
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="p-2 hover:bg-[#ebeff2] rounded-lg transition-colors"
        title="Actions"
      >
        <MoreVertical className="h-5 w-5 text-[#43536d]" />
      </button>

      {isOpen && (
        <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg py-1 z-20">
          {actions.map((action, index) => (
            <button
              key={index}
              onClick={() => {
                action.onClick();
                setIsOpen(false);
              }}
              disabled={action.disabled}
              className={`w-full text-left px-4 py-2 text-sm flex items-center space-x-2 ${
                action.disabled
                  ? 'text-gray-400 cursor-not-allowed'
                  : 'text-[#43536d] hover:bg-[#ebeff2]'
              }`}
            >
              <action.icon className="h-4 w-4" />
              <span>{action.label}</span>
            </button>
          ))}
        </div>
      )}
    </div>
  );
}