import { ApiField } from '../types';

class FieldService {
  updateFieldInTree(fields: ApiField[], updatedField: ApiField): ApiField[] {
    return fields.map(field => {
      if (field.id === updatedField.id) {
        return updatedField;
      }
      if (field.children) {
        return {
          ...field,
          children: this.updateFieldInTree(field.children, updatedField)
        };
      }
      return field;
    });
  }

  removeFieldFromTree(fields: ApiField[], fieldId: string): ApiField[] {
    return fields.filter(field => {
      if (field.id === fieldId) {
        return false;
      }
      if (field.children) {
        field.children = this.removeFieldFromTree(field.children, fieldId);
      }
      return true;
    });
  }

  extractFieldFromTree(fields: ApiField[], fieldId: string): [ApiField | null, ApiField[]] {
    let extracted: ApiField | null = null;
    
    const traverse = (fields: ApiField[]): ApiField[] => {
      return fields.filter(field => {
        if (field.id === fieldId) {
          extracted = field;
          return false;
        }
        if (field.children) {
          field.children = traverse(field.children);
        }
        return true;
      });
    };

    const remaining = traverse(fields);
    return [extracted, remaining];
  }

  insertFieldIntoTree(
    fields: ApiField[],
    fieldToInsert: ApiField,
    targetId: string,
    position: 'before' | 'after' | 'inside'
  ): ApiField[] {
    return fields.map(field => {
      if (field.id === targetId) {
        if (position === 'before') {
          return [fieldToInsert, field];
        } else if (position === 'after') {
          return [field, fieldToInsert];
        } else {
          return {
            ...field,
            children: [...(field.children || []), fieldToInsert]
          };
        }
      }
      if (field.children) {
        return {
          ...field,
          children: this.insertFieldIntoTree(field.children, fieldToInsert, targetId, position)
        };
      }
      return field;
    }).flat();
  }

  createNewField(type: string = 'TextField'): ApiField {
    return {
      id: `${type.toLowerCase()}-${Date.now()}`,
      name: `New ${type}`,
      type: type as any,
      description: 'Enter description here',
      required: false,
      config: {}
    };
  }
}

export const fieldService = new FieldService();