import { ApiField } from '../types';

function generateFieldSchema(field: ApiField): any {
  if (field.hideFromApi) {
    return null;
  }

  if (field.type === 'CompositeField' || field.type === 'FieldGroup') {
    const properties = {};
    const required: string[] = [];

    field.children?.forEach(child => {
      if (!child.hideFromApi) {
        const childSchema = generateFieldSchema(child);
        if (childSchema) {
          properties[child.name] = childSchema;
          if (child.required) {
            required.push(child.name);
          }
        }
      }
    });

    return {
      type: 'object',
      properties,
      ...(required.length > 0 && { required })
    };
  }

  const schema: any = {
    type: getOpenAPIType(field.type),
    description: field.description,
    ...(field.defaultValue && { default: field.defaultValue }),
    ...(field.example && { example: field.example })
  };

  if (field.validation) {
    if (field.validation.min !== undefined) schema.minimum = field.validation.min;
    if (field.validation.max !== undefined) schema.maximum = field.validation.max;
    if (field.validation.pattern) schema.pattern = field.validation.pattern;
  }

  return schema;
}

function getOpenAPIType(fieldType: string): string {
  switch (fieldType) {
    case 'NumericField':
      return 'number';
    case 'CheckboxField':
      return 'boolean';
    case 'DateField':
    case 'TimeField':
    case 'DatetimeField':
      return 'string';
    default:
      return 'string';
  }
}

export function generateOpenAPISpec(fields: ApiField[]) {
  const properties = {};
  const required: string[] = [];

  fields.forEach(field => {
    const schema = generateFieldSchema(field);
    if (schema) {
      properties[field.name] = schema;
      if (field.required) {
        required.push(field.name);
      }
    }
  });

  return {
    openapi: '3.0.0',
    info: {
      title: 'API Documentation',
      version: '1.0.0',
      description: 'Generated API documentation'
    },
    paths: {
      '/api/resource': {
        post: {
          requestBody: {
            required: true,
            content: {
              'application/json': {
                schema: {
                  type: 'object',
                  properties,
                  required
                }
              }
            }
          },
          responses: {
            '200': {
              description: 'Successful response',
              content: {
                'application/json': {
                  schema: {
                    type: 'object',
                    properties,
                    required
                  }
                }
              }
            }
          }
        }
      }
    }
  };
}