import { useState } from 'react';
import { Plus, FileText } from 'lucide-react';
import { SavedDoc } from '../types';
import { documentationService } from '../services/documentationService';
import { TEXT } from '../constants/text';

interface WelcomeScreenProps {
  onCreateDoc: (name: string) => void;
  onSelectDoc: (doc: SavedDoc) => void;
}

export function WelcomeScreen({ onCreateDoc, onSelectDoc }: WelcomeScreenProps) {
  const [isNaming, setIsNaming] = useState(false);
  const [newDocName, setNewDocName] = useState('');
  const savedDocs = documentationService.loadSavedDocs();

  const handleCreateNew = () => {
    setIsNaming(true);
    setNewDocName('');
  };

  const handleSaveConfirm = () => {
    if (!newDocName.trim()) return;
    onCreateDoc(newDocName);
    setIsNaming(false);
    setNewDocName('');
  };

  return (
    <div className="flex-1 p-8">
      <div className="max-w-2xl mx-auto">
        <h1 className="text-3xl font-bold text-[#43536d] mb-8">{TEXT.APP.TITLE}</h1>

        <div className="bg-white rounded-lg shadow-md p-6 mb-8">
          <h2 className="text-xl font-semibold text-[#43536d] mb-4">{TEXT.APP.NEW_DOC}</h2>
          {isNaming ? (
            <div>
              <input
                type="text"
                value={newDocName}
                onChange={(e) => setNewDocName(e.target.value)}
                placeholder={TEXT.DOCS.NEW_DOC_NAME}
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#005a93]"
                autoFocus
                onKeyDown={(e) => {
                  if (e.key === 'Enter') handleSaveConfirm();
                  if (e.key === 'Escape') setIsNaming(false);
                }}
              />
              <div className="flex justify-end mt-4 space-x-2">
                <button
                  onClick={() => setIsNaming(false)}
                  className="px-4 py-2 text-sm text-gray-600 hover:bg-gray-100 rounded"
                >
                  Cancel
                </button>
                <button
                  onClick={handleSaveConfirm}
                  className="px-4 py-2 text-sm bg-[#005a93] text-white rounded hover:bg-[#004b7a]"
                  disabled={!newDocName.trim()}
                >
                  Create
                </button>
              </div>
            </div>
          ) : (
            <button
              onClick={handleCreateNew}
              className="w-full flex items-center justify-center gap-2 px-4 py-3 text-[#005a93] hover:bg-[#ebeff2] rounded-lg border-2 border-dashed border-[#005a93]"
            >
              <Plus className="h-5 w-5" />
              {TEXT.DOCS.CREATE_FIRST}
            </button>
          )}
        </div>

        {savedDocs.length > 0 && (
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold text-[#43536d] mb-4">{TEXT.DOCS.TITLE}</h2>
            <div className="space-y-2">
              {savedDocs.map(doc => (
                <button
                  key={doc.id}
                  onClick={() => onSelectDoc(doc)}
                  className="w-full flex items-center gap-3 p-3 hover:bg-[#ebeff2] rounded-lg text-left"
                >
                  <FileText className="h-5 w-5 text-[#005a93]" />
                  <div>
                    <div className="font-medium text-[#43536d]">{doc.name}</div>
                    <div className="text-sm text-gray-500">
                      {doc.updatedAt}
                    </div>
                  </div>
                </button>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}