import { useState } from 'react';
import { Search } from 'lucide-react';
import { ApiField } from '../types';
import { FIELD_TYPES as defaultTypes } from '../constants/fieldTypes';
import { TEXT } from '../constants/text';

interface TypesListProps {
  onDragStart: (field: ApiField) => void;
  className?: string;
}

export function TypesList({ onDragStart, className = '' }: TypesListProps) {
  const [searchQuery, setSearchQuery] = useState('');

  const filteredTypes = defaultTypes.map(category => ({
    ...category,
    items: category.items.filter(field => 
      field.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      field.description.toLowerCase().includes(searchQuery.toLowerCase())
    )
  })).filter(category => category.items.length > 0);

  return (
    <div className={`w-full bg-gray-50 p-6 border-r h-full overflow-y-auto ${className}`}>
      <h2 className="text-xl font-semibold mb-6">SilverStripe Fields</h2>
      
      <div className="relative mb-6">
        <input
          type="text"
          placeholder="Search fields..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="w-full pl-10 pr-4 py-2.5 border rounded-lg text-base"
        />
        <Search className="absolute left-3 top-3 h-5 w-5 text-gray-400" />
      </div>

      <div className="space-y-8">
         {filteredTypes.length === 0 ? (
            <div className="border-2 border-dashed border-gray-300 rounded-lg p-8 text-center">
              <p className="text-gray-500">
                {TEXT.FIELDS.NO_RESULTS}
              </p>
            </div>
          ) : filteredTypes.map((category) => (
          <div key={category.category}>
            <h3 className="text-base font-medium text-gray-600 mb-3">{category.category}</h3>
            <div className="space-y-3">
              {category.items.map((field) => {
                const Icon = field.icon;
                return (
                  <div
                    key={field.id}
                    draggable
                    onDragStart={(e) => {
                      e.dataTransfer.setData('application/json', JSON.stringify({
                        ...field,
                        required: false,
                        config: {},
                        children: field.type === 'CompositeField' || field.type === 'FieldGroup' ? [] : undefined
                      }));
                      onDragStart(field);
                    }}
                    className="p-3 bg-white border rounded-lg cursor-move hover:shadow-md transition-shadow"
                  >
                    <div className="flex items-center space-x-3">
                      <Icon className="h-5 w-5 text-blue-500" />
                      <span className="font-medium text-base">{field.name}</span>
                    </div>
                    <p className="text-sm text-gray-600 mt-2 leading-relaxed">{field.description}</p>
                  </div>
                );
              })}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}