import React, { useEffect, useRef } from 'react';
import { X } from 'lucide-react';
import { ApiField } from '../types';

interface DrawerProps {
  isOpen: boolean;
  onClose: () => void;
  field: ApiField | null;
  onFieldUpdate: (field: ApiField) => void;
}

export function Drawer({ isOpen, onClose, field, onFieldUpdate }: DrawerProps) {
  const drawerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (drawerRef.current && !drawerRef.current.contains(event.target as Node)) {
        onClose();
      }
    }

    if (isOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen, onClose]);

  if (!isOpen || !field) return null;

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    if (name.startsWith('config.')) {
      const configKey = name.split('.')[1];
      onFieldUpdate({
        ...field,
        config: {
          ...field.config,
          [configKey]: value
        }
      });
    } else {
      onFieldUpdate({ ...field, [name]: value });
    }
  };

  const handleCheckboxChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, checked } = e.target;
    if (name.startsWith('config.')) {
      const configKey = name.split('.')[1];
      onFieldUpdate({
        ...field,
        config: {
          ...field.config ?? {},
          [configKey]: checked
        }
      });
    } else {
      onFieldUpdate({ ...field, [name]: checked });
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 z-50">
      <div ref={drawerRef} className="absolute right-0 top-0 h-full w-96 bg-white shadow-lg">
        <div className="bg-[#005a93] text-white p-4 border-b flex justify-between items-center">
          <h2 className="text-lg font-semibold">Field Settings</h2>
          <button onClick={onClose} className="p-1 hover:bg-[#004b7a] rounded">
            <X className="h-5 w-5" />
          </button>
        </div>
        
        <div className="p-6 space-y-6 overflow-y-auto h-[calc(100vh-64px)]">
          <div className="field">
            <label className="field__label block text-sm font-medium mb-1 text-[#43536d]">Name</label>
            <input
              type="text"
              name="name"
              value={field.name || ''}
              onChange={handleChange}
              className="field__input w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-[#005a93] bg-white"
            />
          </div>

          <div className="field">
            <label className="field__label block text-sm font-medium mb-1 text-[#43536d]">Description</label>
            <textarea
              name="description"
              value={field.description || ''}
              onChange={handleChange}
              rows={3}
              className="field__input w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-[#005a93] bg-white"
            />
          </div>

          <div className="field-group grid grid-cols-2 gap-4">
            <div className="field-checkbox">
              <label className="field__label flex items-center space-x-2">
                <input
                  type="checkbox"
                  id="required"
                  name="required"
                  checked={field.required || false}
                  onChange={handleCheckboxChange}
                  className="form-checkbox h-4 w-4 text-[#005a93] rounded border-gray-300"
                />
                <span className="text-sm font-medium text-[#43536d]">Required</span>
              </label>
            </div>

            <div className="field-checkbox">
              <label className="field__label flex items-center space-x-2">
                <input
                  type="checkbox"
                  id="hideFromApi"
                  name="hideFromApi"
                  checked={field.hideFromApi || false}
                  onChange={handleCheckboxChange}
                  className="form-checkbox h-4 w-4 text-[#005a93] rounded border-gray-300"
                />
                <span className="text-sm font-medium text-[#43536d]">Hide from API</span>
              </label>
            </div>
          </div>

          <div className="field">
            <label className="field__label block text-sm font-medium mb-1 text-[#43536d]">Default Value</label>
            <input
              type="text"
              name="defaultValue"
              value={field.defaultValue || ''}
              onChange={handleChange}
              className="field__input w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-[#005a93] bg-white"
            />
          </div>

          <div className="field">
            <label className="field__label block text-sm font-medium mb-1 text-[#43536d]">Example</label>
            <input
              type="text"
              name="example"
              value={field.example || ''}
              onChange={handleChange}
              className="field__input w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-[#005a93] bg-white"
            />
          </div>

          <div className="border-t border-gray-200 my-6"></div>

          <h3 className="text-lg font-medium text-[#43536d] mb-4">Field Configuration</h3>

          {field.type === 'TextareaField' && (
            <div className="field">
              <label className="field__label block text-sm font-medium mb-1 text-[#43536d]">Rows</label>
              <input
                type="number"
                name="config.rows"
                value={field.config?.rows || ''}
                onChange={handleChange}
                className="field__input w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-[#005a93] bg-white"
              />
            </div>
          )}

          {(field.type === 'DropdownField' || field.type === 'OptionsetField') && (
            <div className="field">
              <label className="field__label block text-sm font-medium mb-1 text-[#43536d]">Options (JSON)</label>
              <textarea
                name="config.options"
                value={JSON.stringify(field.config?.options || {}, null, 2)}
                onChange={(e) => {
                  try {
                    const options = JSON.parse(e.target.value);
                    onFieldUpdate({
                      ...field,
                      config: {
                        ...field.config ?? {},
                        options
                      }
                    });
                  } catch (error) {
                    // Invalid JSON, ignore
                  }
                }}
                rows={4}
                className="field__input w-full p-2 border rounded font-mono text-sm focus:outline-none focus:ring-2 focus:ring-[#005a93] bg-white"
                placeholder='{"key": "value"}'
              />
            </div>
          )}

          <div className="field">
            <label className="field__label block text-sm font-medium mb-1 text-[#43536d]">Placeholder</label>
            <input
              type="text"
              name="config.placeholder"
              value={field.config?.placeholder || ''}
              onChange={handleChange}
              className="field__input w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-[#005a93] bg-white"
            />
          </div>

          <div className="field-group grid grid-cols-2 gap-4">
            <div className="field-checkbox">
              <label className="field__label flex items-center space-x-2">
                <input
                  type="checkbox"
                  id="readonly"
                  name="config.readonly"
                  checked={field.config?.readonly || false}
                  onChange={handleCheckboxChange}
                  className="form-checkbox h-4 w-4 text-[#005a93] rounded border-gray-300"
                />
                <span className="text-sm font-medium text-[#43536d]">Readonly</span>
              </label>
            </div>

            <div className="field-checkbox">
              <label className="field__label flex items-center space-x-2">
                <input
                  type="checkbox"
                  id="disabled"
                  name="config.disabled"
                  checked={field.config?.disabled || false}
                  onChange={handleCheckboxChange}
                  className="form-checkbox h-4 w-4 text-[#005a93] rounded border-gray-300"
                />
                <span className="text-sm font-medium text-[#43536d]">Disabled</span>
              </label>
            </div>
          </div>

          <div className="field">
            <label className="field__label block text-sm font-medium mb-1 text-[#43536d]">CSS Class</label>
            <input
              type="text"
              name="config.cssClass"
              value={field.config?.cssClass || ''}
              onChange={handleChange}
              className="field__input w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-[#005a93] bg-white"
            />
          </div>

          <div className="field">
            <label className="field__label block text-sm font-medium mb-1 text-[#43536d]">Right Title</label>
            <input
              type="text"
              name="config.rightTitle"
              value={field.config?.rightTitle || ''}
              onChange={handleChange}
              className="field__input w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-[#005a93] bg-white"
            />
          </div>

          <div className="field">
            <label className="field__label block text-sm font-medium mb-1 text-[#43536d]">Left Title</label>
            <input
              type="text"
              name="config.leftTitle"
              value={field.config?.leftTitle || ''}
              onChange={handleChange}
              className="field__input w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-[#005a93] bg-white"
            />
          </div>
        </div>
      </div>
    </div>
  );
}