import { useState, useEffect } from 'react';
import { Save, FolderOpen, Trash2, FileText, Plus, MoreVertical, Edit, Globe, Copy, ChevronLeft, ChevronRight } from 'lucide-react';
import { ApiField, SavedDoc } from '../types';
import { documentationService } from '../services/documentationService';
import { updateApi } from '../services/apiService';

interface DocumentationPanelProps {
  fields: ApiField[];
  onLoadFields: (fields: ApiField[]) => void;
  doc: SavedDoc;
}

export function DocumentationPanel({ fields, onLoadFields, doc }: DocumentationPanelProps) {
  const [savedDocs, setSavedDocs] = useState<SavedDoc[]>([]);
  const [isNaming, setIsNaming] = useState(false);
  const [newDocName, setNewDocName] = useState('');
  const [selectedDoc, setSelectedDoc] = useState<string | null>(null);
  const [openActionMenu, setOpenActionMenu] = useState<string | null>(null);
  const [editingDoc, setEditingDoc] = useState<string | null>(null);
  const [showApiDetails, setShowApiDetails] = useState<string | null>(null);
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);

  useEffect(() => {
    const saved = localStorage.getItem(documentationService.getStorageKey());
    if (saved) {
      setSavedDocs(JSON.parse(saved));
    }
    
  }, []);

  if (!selectedDoc && doc?.id) {
    setSelectedDoc(doc.id);
  }
  
  const handleCreateNew = () => {
    setIsNaming(true);
    setNewDocName('');
  };

  const handleSave = async (docId: string) => {
    try {
      const updated = documentationService.updateDocs(savedDocs, fields, docId);
      await updateApi(updated.find(doc => doc.id === docId) as SavedDoc);
      setSavedDocs(updated);
      documentationService.saveDocs(updated);
    } catch (error) {
      console.error('Error:', error);
    }
    
    setOpenActionMenu(null);
  };

  const handleSaveConfirm = () => {
    if (!newDocName.trim()) return;

    const newDoc = documentationService.createNewDoc(newDocName, []);
    const updated = [...savedDocs, newDoc];
    documentationService.saveDocs(updated);

    setSavedDocs(updated);
    setIsNaming(false);
    setNewDocName('');
    setSelectedDoc(newDoc.id);
  };

  const handleDelete = (id: string) => {
    const updated = savedDocs.filter(doc => doc.id !== id);
    setSavedDocs(updated);
    localStorage.setItem(documentationService.getStorageKey(), JSON.stringify(updated));
    if (selectedDoc === id) {
      setSelectedDoc(null);
    }
    setOpenActionMenu(null);
  };

  const handleLoad = (doc: SavedDoc) => {
    onLoadFields(doc.fields);
    setSelectedDoc(doc.id);
    setOpenActionMenu(null);
  };

  const handleRename = async (id: string) => {
    try {
      await updateApi(savedDocs.find(doc => doc.id === id) as SavedDoc);
      const updated = documentationService.renameDoc(savedDocs, id);
      console.log(Date().toString());
      
      documentationService.saveDocs(updated);
    } catch (error) {
      console.error('Error:', error);
    }
    
    setEditingDoc(null);
  };

  const handleCopyToClipboard = (text: string) => {
    navigator.clipboard.writeText(text);
  };

  return (
    <div className={`relative transition-all duration-300 ${isSidebarOpen ? 'w-64' : 'w-0'}`}>
        <button
          onClick={() => setIsSidebarOpen(!isSidebarOpen)}
          className={`absolute ${isSidebarOpen ? '-left-3' : '-left-4'}  top-1/2 transform -translate-y-1/2 bg-white p-1 rounded-full shadow-md border`}
        >
          {isSidebarOpen ? <ChevronRight className="h-4 w-4" /> :  <ChevronLeft className="h-4 w-4" />}
        </button>
      
      <div className={`w-64 border-l bg-gray-50 p-4 flex flex-col h-full`}>
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-lg font-semibold text-[#43536d]">Saved Docs</h2>
          <button
            onClick={handleCreateNew}
            className="p-2 hover:bg-gray-200 rounded-lg transition-colors"
            title="Create new documentation"
          >
            <Plus className="h-5 w-5 text-[#005a93]" />
          </button>
        </div>

        {isNaming && (
          <div className="mb-4">
            <input
              type="text"
              value={newDocName}
              onChange={(e) => setNewDocName(e.target.value)}
              placeholder="Documentation name"
              className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#005a93]"
              autoFocus
              onKeyDown={(e) => {
                if (e.key === 'Enter') handleSaveConfirm();
                if (e.key === 'Escape') setIsNaming(false);
              }}
            />
            <div className="flex justify-end mt-2 space-x-2">
              <button
                onClick={() => setIsNaming(false)}
                className="px-3 py-1 text-sm text-gray-600 hover:bg-gray-200 rounded"
              >
                Cancel
              </button>
              <button
                onClick={handleSaveConfirm}
                className="px-3 py-1 text-sm bg-[#005a93] text-white rounded hover:bg-[#004b7a]"
                disabled={!newDocName.trim()}
              >
                Save
              </button>
            </div>
          </div>
        )}

        <div className="flex-1 overflow-y-auto -mx-4 px-4">
          {savedDocs.length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              <FileText className="h-12 w-12 mx-auto mb-2 opacity-50" />
              <p className="text-sm">No saved documentation</p>
              <button
                onClick={handleCreateNew}
                className="mt-4 flex items-center justify-center gap-2 px-4 py-2 text-sm text-[#005a93] hover:bg-[#ebeff2] rounded-lg mx-auto"
              >
                <Plus className="h-4 w-4" />
                Create New
              </button>
            </div>
          ) : (
            <div className="space-y-2">
              {savedDocs.map(doc => (
                <div
                  key={doc.id}
                  className={`p-3 rounded-lg transition-colors ${
                    selectedDoc === doc.id ? 'bg-[#ebeff2] border border-[#005a93]' : 'hover:bg-[#ebeff2]'
                  }`}
                >
                  <div className="flex items-center justify-between mb-1">
                    {editingDoc === doc.id ? (
                      <input
                        type="text"
                        value={doc.name}
                        onChange={(e) => {
                          const updated = savedDocs.map(d => 
                            d.id === doc.id ? { ...d, name: e.target.value } : d
                          );
                          setSavedDocs(updated);
                        }}
                        onKeyDown={(e) => {
                          if (e.key === 'Enter') handleRename(doc.id);
                          if (e.key === 'Escape') setEditingDoc(null);
                        }}
                        onBlur={() => handleRename(doc.id)}
                        className="px-2 py-1 w-full border rounded focus:outline-none focus:ring-2 focus:ring-[#005a93]"
                        autoFocus
                      />
                    ) : (
                      <h3 className="font-medium text-[#43536d] truncate">{doc.name}</h3>
                    )}
                    <div className="relative">
                      <button
                        onClick={() => setOpenActionMenu(openActionMenu === doc.id ? null : doc.id)}
                        className="p-1 hover:bg-gray-200 rounded"
                      >
                        <MoreVertical className="h-4 w-4 text-gray-500" />
                      </button>
                      {openActionMenu === doc.id && (
                        <div onBlur={() => setOpenActionMenu(null)} className="absolute right-0 mt-1 w-48 bg-white rounded-md shadow-lg z-10 py-1">
                          <button
                            onClick={() => handleLoad(doc)}
                            className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 flex items-center"
                          >
                            <FolderOpen className="h-4 w-4 mr-2" />
                            Load
                          </button>
                          <button
                            onClick={() => handleSave(doc.id)}
                            className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 flex items-center"
                          >
                            <Save className="h-4 w-4 mr-2" />
                            Update
                          </button>
                          <button
                            onClick={() => setEditingDoc(doc.id)}
                            className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 flex items-center"
                          >
                            <Edit className="h-4 w-4 mr-2" />
                            Rename
                          </button>
                          <button
                            onClick={() => setShowApiDetails(showApiDetails === doc.id ? null : doc.id)}
                            className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 flex items-center"
                          >
                            <Globe className="h-4 w-4 mr-2" />
                            API Details
                          </button>
                          <button
                            onClick={() => handleDelete(doc.id)}
                            className="w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-red-50 flex items-center"
                          >
                            <Trash2 className="h-4 w-4 mr-2" />
                            Delete
                          </button>
                        </div>
                      )}
                    </div>
                  </div>
                  <div className="text-xs text-gray-500">
                    {new Date(doc.updatedAt).toLocaleDateString()}
                  </div>

                  {showApiDetails === doc.id && (
                    <div className="mt-3 p-3 bg-white rounded-lg border text-sm">
                      <div className="mb-2">
                        <div className="flex items-center justify-between text-[#43536d] mb-1">
                          <span className="font-medium">API Endpoint</span>
                          <button
                            onClick={() => handleCopyToClipboard(doc.apiEndpoint || '')}
                            className="p-1 hover:bg-gray-100 rounded"
                            title="Copy endpoint"
                          >
                            <Copy className="h-3 w-3" />
                          </button>
                        </div>
                        <code className="text-xs bg-[#ebeff2] px-2 py-1 rounded block">
                          {doc.apiEndpoint}
                        </code>
                      </div>
                      <div>
                        <div className="flex items-center justify-between text-[#43536d] mb-1">
                          <span className="font-medium">API Key</span>
                          <button
                            onClick={() => handleCopyToClipboard(doc.apiKey || '')}
                            className="p-1 hover:bg-gray-100 rounded"
                            title="Copy API key"
                          >
                            <Copy className="h-3 w-3" />
                          </button>
                        </div>
                        <code className="text-xs bg-[#ebeff2] px-2 py-1 rounded block">
                          {doc.apiKey}
                        </code>
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}