import { ApiField, SavedDoc } from '../types';

class DocumentationService {
  private readonly STORAGE_KEY = import.meta.env.VITE_STORAGE_KEY ?? 'savedDocumentation';

  loadSavedDocs(): SavedDoc[] {
    const saved = localStorage.getItem(this.STORAGE_KEY);
    return saved ? JSON.parse(saved) : [];
  }

  saveDocs(docs: SavedDoc[]): void {
    localStorage.setItem(this.STORAGE_KEY, JSON.stringify(docs));
  }

  generateApiKey(): string {
    const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    let key = '';
    for (let i = 0; i < 32; i++) {
      key += chars.charAt(Math.floor(Math.random() * chars.length));
    }
    return key;
  }

  generateApiEndpoint(): string {
    return `/api/docs/${Date.now().toString(36)}`;
  }

  createNewDoc(name: string, fields: ApiField[]): SavedDoc {
    return {
      id: Date.now().toString(),
      name: name.trim(),
      fields,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
      apiEndpoint: this.generateApiEndpoint(),
      apiKey: this.generateApiKey()
    };
  }

  updateDoc(doc: SavedDoc, fields: ApiField[]): SavedDoc {
    return {
      ...doc,
      fields,
      updatedAt: new Date().toISOString(),
    };
  }
  // update fields
  updateDocs(savedDocs: SavedDoc[], fields: ApiField[], docId: string): SavedDoc[] {
    const doc = savedDocs.find(d => d.id === docId);

    if (doc) {
      const updated = savedDocs.map(d => {
        if (d.id === docId) {
          return {
            ...d,
            fields,
            updatedAt: new Date().toISOString(),
          };
        }
        return d;
      });

      return updated;
    }
    
    return savedDocs
  }

  renameDoc(savedDocs: SavedDoc[], docId: string): SavedDoc[] {
    const updated = savedDocs.map(doc => {
      if (doc.id === docId) {
        return {
          ...doc,
          updatedAt: new Date().toISOString(),
        };
      }
      return doc;
    });
    return updated;
  }

  getStorageKey(): string {
    return this.STORAGE_KEY;
  }
}

export const documentationService = new DocumentationService();