import { useState } from 'react';
import { MainLayout } from './components/layout/MainLayout';
import { WelcomeScreen } from './components/WelcomeScreen';
import { DocumentationBuilder } from './components/DocumentationBuilder';
import { SavedDoc } from './types';
import { documentationService } from './services/documentationService';
import { createApi } from './services/apiService';

function App() {
  const [selectedDoc, setSelectedDoc] = useState<SavedDoc | null>(null);

  const handleCreateDoc = async (name: string) => {
    const newDoc = documentationService.createNewDoc(name, []);
    const docs = documentationService.loadSavedDocs();
    
    try {
      await createApi(newDoc)
      documentationService.saveDocs([newDoc, ...docs]);
      setSelectedDoc(newDoc);
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const handleSelectDoc = (doc: SavedDoc) => {
    setSelectedDoc(doc);
  };

  return (
    <MainLayout>
      {selectedDoc ? (
        <DocumentationBuilder
          doc={selectedDoc}
        />
      ) : (
        <WelcomeScreen
          onCreateDoc={handleCreateDoc}
          onSelectDoc={handleSelectDoc}
        />
      )}
    </MainLayout>
  );
}

export default App;